import Toast from '@/wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			showPass: 'unapproval',
			pendingLists: [],
			active: 0,
			show: false,
			studentId: "",
			classLists: []
		}
	},
	methods: {
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 切换审批状态
		change(val) {
			this.showPass = val
		},
		// 获取待分配的学生列表
		pendingList() {
			this.$minApi.pendingList({}).then(data => {
				this.pendingLists = data.data
				// console.log(this.pendingLists[0].studentAccount)
			})
		},
		// 选中mtab
		onChange(event) {
			console.log(event)
		},
		// 通过
		adopt() {
			Toast("通过")
		},
		// 不通过
		notPass() {
			Toast("不通过")
		},
		// 未分配

		// 显示选择班级弹框
		changeRanking(studentId) {
			this.show = true;
			this.studentId = studentId
		},
		// picker选择器

		// 确认班级
		onConfirm(event) {
			const {
				picker,
				value,
				index
			} = event.detail;
			var classId = value.classId;
			var text = value.text;
			// console.log('当前值' + text + '当前索引' + keyId);
			this.show = false;
			this.$minApi.assignStudent({
				classId: classId,
				studentId: this.studentId
			}).then(data => {
				if (data.code == 200) {
					Toast("加入班级成功！")
				} else {
					Toast(data.msg)
				}
				// console.log(data)
			})
		},
		// 取消按钮
		onCancel() {
			this.show = false;
		},
		// 获取班级下拉列表
		classList() {
			this.$minApi.getClassList({
				schoolId: 1
			}).then(data => {
				data.data.forEach(val => {
					val.text = val.className
				})
				this.classLists = data.data
			})
		}

	},
	created() {
		this.pendingList()
		this.classList()
	}

}
